/*
 ** 处理顶部导航的action
 ** @param state{object} 状态
 ** @param action(string) action事件
 */
import {
	fromJS,
} from 'immutable'
import {
	GET_NEWS_CONTENT
} from './action'

	
const initialState = fromJS({
	content:{}
});

function reducer(state = initialState, action) {
	switch (action.type) {
		case GET_NEWS_CONTENT+'_SUCCESS':
			console.log(action);
		return state.set('content',action.payload.result.content);
		default:
			return state;
	}
}
export default reducer;


