import {
	CALL_API
} from 'redux-api-middleware'


function callAPP({api,action,meta={}}) {
	return {
		[CALL_API]: {
			endpoint: api,
			method: 'GET',
			
			types: [
				action+'_REQUEST', {
					type: action+'_SUCCESS',
					payload: (action, state, res) => {
						return res.json().then((json) => json);
					},
				},
				action+'_FAILURE'
			]
		}
	}
}
export default callAPP;


